import { compose, withProps } from 'recompose';
import { tagsRegistry } from '../../lib/tags_registry';
import { TagList as Component } from './tag_list';
export const TagList = compose(withProps(() => ({
  getTag: tag => tagsRegistry.get(tag) || {
    name: tag,
    color: undefined
  }
})))(Component);