import { ZOOM_LEVELS, MAX_ZOOM_LEVEL, MIN_ZOOM_LEVEL } from '../../common/lib/constants';
// handlers for zooming in and out
export const zoomHandlerCreators = {
  zoomIn: ({
    zoomScale,
    setZoomScale
  }) => () => {
    const scaleUp = ZOOM_LEVELS.find(zoomLevel => zoomScale < zoomLevel) || MAX_ZOOM_LEVEL;
    setZoomScale(scaleUp);
  },
  zoomOut: ({
    zoomScale,
    setZoomScale
  }) => () => {
    const scaleDown = ZOOM_LEVELS.slice().reverse().find(zoomLevel => zoomScale > zoomLevel) || MIN_ZOOM_LEVEL;
    setZoomScale(scaleDown);
  },
  resetZoom: ({
    setZoomScale
  }) => () => {
    setZoomScale(1);
  }
};