import { connect } from 'react-redux';
import { compose, withHandlers } from 'recompose';
import { zoomHandlerCreators } from '../../../lib/app_handler_creators';
// @ts-expect-error untyped local
import { fetchAllRenderables } from '../../../state/actions/elements'; // @ts-expect-error untyped local

import { setZoomScale, setFullscreen, selectToplevelNodes } from '../../../state/actions/transient';
import { setWriteable, setRefreshInterval, enableAutoplay, setAutoplayInterval } from '../../../state/actions/workpad';
import { getZoomScale, canUserWrite } from '../../../state/selectors/app';
import { getWorkpadBoundingBox, getWorkpadWidth, getWorkpadHeight, isWriteable, getRefreshInterval, getAutoplay } from '../../../state/selectors/workpad';
import { ViewMenu as Component } from './view_menu.component';
import { getFitZoomScale } from './lib/get_fit_zoom_scale';

const mapStateToProps = state => {
  const {
    enabled,
    interval
  } = getAutoplay(state);
  return {
    zoomScale: getZoomScale(state),
    boundingBox: getWorkpadBoundingBox(state),
    workpadWidth: getWorkpadWidth(state),
    workpadHeight: getWorkpadHeight(state),
    isWriteable: isWriteable(state) && canUserWrite(state),
    refreshInterval: getRefreshInterval(state),
    autoplayEnabled: enabled,
    autoplayInterval: interval
  };
};

const mapDispatchToProps = dispatch => ({
  setZoomScale: scale => dispatch(setZoomScale(scale)),
  setWriteable: isWorkpadWriteable => dispatch(setWriteable(isWorkpadWriteable)),
  setFullscreen: value => {
    dispatch(setFullscreen(value));

    if (value) {
      dispatch(selectToplevelNodes([]));
    }
  },
  doRefresh: () => dispatch(fetchAllRenderables()),
  setRefreshInterval: interval => dispatch(setRefreshInterval(interval)),
  enableAutoplay: autoplay => dispatch(enableAutoplay(!!autoplay)),
  setAutoplayInterval: interval => dispatch(setAutoplayInterval(interval))
});

const mergeProps = (stateProps, dispatchProps, ownProps) => {
  const {
    boundingBox,
    workpadWidth,
    workpadHeight,
    ...remainingStateProps
  } = stateProps;
  return { ...remainingStateProps,
    ...dispatchProps,
    ...ownProps,
    toggleWriteable: () => dispatchProps.setWriteable(!stateProps.isWriteable),
    enterFullscreen: () => dispatchProps.setFullscreen(true),
    fitToWindow: () => dispatchProps.setZoomScale(getFitZoomScale(boundingBox, workpadWidth, workpadHeight))
  };
};

export const ViewMenu = compose(connect(mapStateToProps, mapDispatchToProps, mergeProps), withHandlers(zoomHandlerCreators))(Component);