import { API_ROUTE_CUSTOM_ELEMENT } from '../../common/lib/constants';
import { fetch } from '../../common/lib/fetch';
import { platformService } from '../services';

const getApiPath = function () {
  const basePath = platformService.getService().getBasePath();
  return `${basePath}${API_ROUTE_CUSTOM_ELEMENT}`;
};

export const create = customElement => fetch.post(getApiPath(), customElement);
export const get = customElementId => fetch.get(`${getApiPath()}/${customElementId}`).then(({
  data: element
}) => element);
export const update = (id, element) => fetch.put(`${getApiPath()}/${id}`, element);
export const remove = id => fetch.delete(`${getApiPath()}/${id}`);
export const find = async (searchTerm) => {
  const validSearchTerm = typeof searchTerm === 'string' && searchTerm.length > 0;
  return fetch.get(`${getApiPath()}/find?name=${validSearchTerm ? searchTerm : ''}&perPage=10000`).then(({
    data: customElements
  }) => customElements);
};