import React from 'react';
import { connect } from 'react-redux';
import { compose, withProps } from 'recompose';
// @ts-expect-error untyped local
import { elementsRegistry } from '../../../lib/elements_registry';
import { ElementMenu as Component } from './element_menu.component'; // @ts-expect-error untyped local

import { addElement } from '../../../state/actions/elements';
import { getSelectedPage } from '../../../state/selectors/workpad';
import { AddEmbeddablePanel } from '../../embeddable_flyout';

const mapStateToProps = state => ({
  pageId: getSelectedPage(state)
});

const mapDispatchToProps = dispatch => ({
  addElement: pageId => element => dispatch(addElement(pageId, element))
});

const mergeProps = (stateProps, dispatchProps) => ({ ...stateProps,
  ...dispatchProps,
  addElement: dispatchProps.addElement(stateProps.pageId),
  // Moved this section out of the main component to enable stories
  renderEmbedPanel: onClose => /*#__PURE__*/React.createElement(AddEmbeddablePanel, {
    onClose: onClose
  })
});

export const ElementMenu = compose(connect(mapStateToProps, mapDispatchToProps, mergeProps), withProps(() => ({
  elements: elementsRegistry.toJS()
})))(Component);