import { connect } from 'react-redux';
import { get } from 'lodash';
import { getId } from '../../lib/get_id'; // @ts-expect-error untyped local

import { findExistingAsset } from '../../lib/find_existing_asset';
import { VALID_IMAGE_TYPES } from '../../../common/lib/constants';
import { encode } from '../../../common/lib/dataurl'; // @ts-expect-error untyped local

import { getAssets } from '../../state/selectors/assets'; // @ts-expect-error untyped local

import { createAsset } from '../../state/actions/assets';
import { AssetManager as Component } from './asset_manager.component';
export const AssetManager = connect(state => ({
  assets: getAssets(state)
}), dispatch => ({
  onAddAsset: (type, content) => {
    // make the ID here and pass it into the action
    const assetId = getId('asset');
    dispatch(createAsset(type, content, assetId)); // then return the id, so the caller knows the id that will be created

    return assetId;
  }
}), (stateProps, dispatchProps, ownProps) => {
  const {
    assets
  } = stateProps;
  const {
    onAddAsset
  } = dispatchProps; // pull values out of assets object
  // have to cast to AssetType[] because TS doesn't know about filtering

  const assetValues = Object.values(assets).filter(asset => !!asset);
  return { ...ownProps,
    assets: assetValues,
    onAddAsset: file => {
      const [type, subtype] = get(file, 'type', '').split('/');

      if (type === 'image' && VALID_IMAGE_TYPES.indexOf(subtype) >= 0) {
        return encode(file).then(dataurl => {
          const dataurlType = 'dataurl';
          const existingId = findExistingAsset(dataurlType, dataurl, assetValues);

          if (existingId) {
            return existingId;
          }

          return onAddAsset(dataurlType, dataurl);
        });
      }

      return false;
    }
  };
})(Component);