import { camelCase } from 'lodash';
import { getClipboardData, setClipboardData } from './clipboard';
import { cloneSubgraphs } from './clone_subgraphs';
import { notifyService } from '../services';
import * as customElementService from './custom_element_service';
import { getId } from './get_id';
import { ELEMENT_NUDGE_OFFSET, ELEMENT_SHIFT_OFFSET } from '../../common/lib/constants';

const extractId = node => node.id;

// handlers for clone, delete, and saving custom elements
export const basicHandlerCreators = {
  cloneNodes: ({
    insertNodes,
    pageId,
    selectToplevelNodes,
    selectedNodes
  }) => () => {
    const clonedNodes = selectedNodes && cloneSubgraphs(selectedNodes);

    if (clonedNodes) {
      insertNodes(clonedNodes, pageId);
      selectToplevelNodes(clonedNodes);
    }
  },
  deleteNodes: ({
    pageId,
    removeNodes,
    selectedNodes
  }) => () => {
    if (selectedNodes.length) {
      removeNodes(selectedNodes.map(extractId), pageId);
    }
  },
  createCustomElement: ({
    selectedNodes
  }) => (name = '', description = '', image = '') => {
    if (selectedNodes.length) {
      const content = JSON.stringify({
        selectedNodes
      });
      const customElement = {
        id: getId('custom-element'),
        name: camelCase(name),
        displayName: name,
        help: description,
        image,
        content
      };
      customElementService.create(customElement).then(() => notifyService.getService().success(`Custom element '${customElement.displayName || customElement.id}' was saved`, {
        'data-test-subj': 'canvasCustomElementCreate-success'
      })).catch(error => notifyService.getService().warning(error, {
        title: `Custom element '${customElement.displayName || customElement.id}' was not saved`
      }));
    }
  }
}; // handlers for alignment and distribution

export const alignmentDistributionHandlerCreators = Object.assign({}, ...['alignLeft', 'alignCenter', 'alignRight', 'alignTop', 'alignMiddle', 'alignBottom', 'distributeHorizontally', 'distributeVertically'].map(event => ({
  [event]: ({
    commit
  }) => () => {
    commit('actionEvent', {
      event
    });
  }
}))); // handlers for group and ungroup

export const groupHandlerCreators = {
  groupNodes: ({
    commit
  }) => () => {
    commit('actionEvent', {
      event: 'group'
    });
  },
  ungroupNodes: ({
    commit
  }) => () => {
    commit('actionEvent', {
      event: 'ungroup'
    });
  }
}; // handlers for cut/copy/paste

export const clipboardHandlerCreators = {
  cutNodes: ({
    pageId,
    removeNodes,
    selectedNodes
  }) => () => {
    if (selectedNodes.length) {
      setClipboardData({
        selectedNodes
      });
      removeNodes(selectedNodes.map(extractId), pageId);
      notifyService.getService().success('Cut element to clipboard');
    }
  },
  copyNodes: ({
    selectedNodes
  }) => () => {
    if (selectedNodes.length) {
      setClipboardData({
        selectedNodes
      });
      notifyService.getService().success('Copied element to clipboard');
    }
  },
  pasteNodes: ({
    insertNodes,
    pageId,
    selectToplevelNodes
  }) => () => {
    const {
      selectedNodes = []
    } = JSON.parse(getClipboardData()) || {};
    const clonedNodes = selectedNodes && cloneSubgraphs(selectedNodes);

    if (clonedNodes) {
      insertNodes(clonedNodes, pageId); // first clone and persist the new node(s)

      selectToplevelNodes(clonedNodes); // then select the cloned node(s)
    }
  }
}; // handlers for changing element layer position
// TODO: support relayering multiple elements

export const layerHandlerCreators = {
  bringToFront: ({
    elementLayer,
    pageId,
    selectedNodes
  }) => () => {
    if (selectedNodes.length === 1) {
      elementLayer(pageId, selectedNodes[0].id, Infinity);
    }
  },
  bringForward: ({
    elementLayer,
    pageId,
    selectedNodes
  }) => () => {
    if (selectedNodes.length === 1) {
      elementLayer(pageId, selectedNodes[0].id, 1);
    }
  },
  sendBackward: ({
    elementLayer,
    pageId,
    selectedNodes
  }) => () => {
    if (selectedNodes.length === 1) {
      elementLayer(pageId, selectedNodes[0].id, -1);
    }
  },
  sendToBack: ({
    elementLayer,
    pageId,
    selectedNodes
  }) => () => {
    if (selectedNodes.length === 1) {
      elementLayer(pageId, selectedNodes[0].id, -Infinity);
    }
  }
}; // handlers for shifting elements up, down, left, and right

export const positionHandlerCreators = {
  shiftUp: ({
    selectedNodes,
    setMultiplePositions
  }) => () => {
    setMultiplePositions(selectedNodes.map(element => {
      element.position.top -= ELEMENT_SHIFT_OFFSET;
      return element;
    }));
  },
  shiftDown: ({
    selectedNodes,
    setMultiplePositions
  }) => () => {
    setMultiplePositions(selectedNodes.map(element => {
      element.position.top += ELEMENT_SHIFT_OFFSET;
      return element;
    }));
  },
  shiftLeft: ({
    selectedNodes,
    setMultiplePositions
  }) => () => {
    setMultiplePositions(selectedNodes.map(element => {
      element.position.left -= ELEMENT_SHIFT_OFFSET;
      return element;
    }));
  },
  shiftRight: ({
    selectedNodes,
    setMultiplePositions
  }) => () => {
    setMultiplePositions(selectedNodes.map(element => {
      element.position.left += ELEMENT_SHIFT_OFFSET;
      return element;
    }));
  },
  nudgeUp: ({
    selectedNodes,
    setMultiplePositions
  }) => () => {
    setMultiplePositions(selectedNodes.map(element => {
      element.position.top -= ELEMENT_NUDGE_OFFSET;
      return element;
    }));
  },
  nudgeDown: ({
    selectedNodes,
    setMultiplePositions
  }) => () => {
    setMultiplePositions(selectedNodes.map(element => {
      element.position.top += ELEMENT_NUDGE_OFFSET;
      return element;
    }));
  },
  nudgeLeft: ({
    selectedNodes,
    setMultiplePositions
  }) => () => {
    setMultiplePositions(selectedNodes.map(element => {
      element.position.left -= ELEMENT_NUDGE_OFFSET;
      return element;
    }));
  },
  nudgeRight: ({
    selectedNodes,
    setMultiplePositions
  }) => () => {
    setMultiplePositions(selectedNodes.map(element => {
      element.position.left += ELEMENT_NUDGE_OFFSET;
      return element;
    }));
  }
};