import { connect } from 'react-redux';
import { compose, withState } from 'recompose';
import { camelCase } from 'lodash';
import { cloneSubgraphs } from '../../lib/clone_subgraphs';
import * as customElementService from '../../lib/custom_element_service';
import { withServices } from '../../services'; // @ts-expect-error untyped local

import { selectToplevelNodes } from '../../state/actions/transient'; // @ts-expect-error untyped local

import { insertNodes } from '../../state/actions/elements';
import { getSelectedPage } from '../../state/selectors/workpad';
import { trackCanvasUiMetric, METRIC_TYPE } from '../../lib/ui_metric';
import { SavedElementsModal as Component } from './saved_elements_modal.component';
const customElementAdded = 'elements-custom-added';

const mapStateToProps = state => ({
  pageId: getSelectedPage(state)
});

const mapDispatchToProps = dispatch => ({
  selectToplevelNodes: nodes => dispatch(selectToplevelNodes(nodes.filter(e => !e.position.parent).map(e => e.id))),
  insertNodes: (selectedNodes, pageId) => dispatch(insertNodes(selectedNodes, pageId))
});

const mergeProps = (stateProps, dispatchProps, ownProps) => {
  const {
    pageId
  } = stateProps;
  const {
    onClose,
    search,
    setCustomElements
  } = ownProps;

  const findCustomElements = async () => {
    const {
      customElements
    } = await customElementService.find(search);
    setCustomElements(customElements);
  };

  return { ...ownProps,
    // add custom element to the page
    addCustomElement: customElement => {
      const {
        selectedNodes = []
      } = JSON.parse(customElement.content) || {};
      const clonedNodes = selectedNodes && cloneSubgraphs(selectedNodes);

      if (clonedNodes) {
        dispatchProps.insertNodes(clonedNodes, pageId); // first clone and persist the new node(s)

        dispatchProps.selectToplevelNodes(clonedNodes); // then select the cloned node(s)
      }

      onClose();
      trackCanvasUiMetric(METRIC_TYPE.LOADED, customElementAdded);
    },
    // custom element search
    findCustomElements: async text => {
      try {
        await findCustomElements();
      } catch (err) {
        ownProps.services.notify.error(err, {
          title: `Couldn't find custom elements`
        });
      }
    },
    // remove custom element
    removeCustomElement: async id => {
      try {
        await customElementService.remove(id);
        await findCustomElements();
      } catch (err) {
        ownProps.services.notify.error(err, {
          title: `Couldn't delete custom elements`
        });
      }
    },
    // update custom element
    updateCustomElement: async (id, name, description, image) => {
      try {
        await customElementService.update(id, {
          name: camelCase(name),
          displayName: name,
          image,
          help: description
        });
        await findCustomElements();
      } catch (err) {
        ownProps.services.notify.error(err, {
          title: `Couldn't update custom elements`
        });
      }
    }
  };
};

export const SavedElementsModal = compose(withServices, withState('search', 'setSearch', ''), withState('customElements', 'setCustomElements', []), connect(mapStateToProps, mapDispatchToProps, mergeProps))(Component);