import { connect } from 'react-redux';
import { compose, withHandlers, withProps } from 'recompose';
import { getClipboardData } from '../../../lib/clipboard'; // @ts-expect-error untyped local

import { flatten } from '../../../lib/aeroelastic/functional'; // @ts-expect-error untyped local

import { globalStateUpdater } from '../../workpad_page/integration_utils'; // @ts-expect-error untyped local

import { crawlTree } from '../../workpad_page/integration_utils'; // @ts-expect-error untyped local

import { insertNodes, elementLayer, removeElements } from '../../../state/actions/elements'; // @ts-expect-error untyped local

import { undoHistory, redoHistory } from '../../../state/actions/history'; // @ts-expect-error untyped local

import { selectToplevelNodes } from '../../../state/actions/transient';
import { getSelectedPage, getNodes, getSelectedToplevelNodes } from '../../../state/selectors/workpad';
import { layerHandlerCreators, clipboardHandlerCreators, basicHandlerCreators, groupHandlerCreators, alignmentDistributionHandlerCreators } from '../../../lib/element_handler_creators';
import { EditMenu as Component } from './edit_menu.component';

const withGlobalState = (commit, updateGlobalState) => (type, payload) => {
  const newLayoutState = commit(type, payload);

  if (newLayoutState.currentScene.gestureEnd) {
    updateGlobalState(newLayoutState);
  }
};
/*
 * TODO: this is all copied from interactive_workpad_page and workpad_shortcuts
 */


const mapStateToProps = state => {
  const pageId = getSelectedPage(state);
  const nodes = getNodes(state, pageId);
  const selectedToplevelNodes = getSelectedToplevelNodes(state);
  const selectedPrimaryShapeObjects = selectedToplevelNodes.map(id => nodes.find(s => s.id === id)).filter(shape => shape);
  const selectedPersistentPrimaryNodes = flatten(selectedPrimaryShapeObjects.map(shape => nodes.find(n => n.id === shape.id) // is it a leaf or a persisted group?
  ? [shape.id] : nodes.filter(s => s.position.parent === shape.id).map(s => s.id)));
  const selectedNodeIds = flatten(selectedPersistentPrimaryNodes.map(crawlTree(nodes)));
  const selectedNodes = selectedNodeIds.map(id => nodes.find(s => s.id === id)).filter(node => {
    return !!node;
  });
  return {
    pageId,
    selectedToplevelNodes,
    selectedNodes,
    state
  };
};

const mapDispatchToProps = dispatch => ({
  insertNodes: (selectedNodes, pageId) => dispatch(insertNodes(selectedNodes, pageId)),
  removeNodes: (nodeIds, pageId) => dispatch(removeElements(nodeIds, pageId)),
  selectToplevelNodes: nodes => dispatch(selectToplevelNodes(nodes.filter(e => !e.position.parent).map(e => e.id))),
  elementLayer: (pageId, elementId, movement) => {
    dispatch(elementLayer({
      pageId,
      elementId,
      movement
    }));
  },
  undoHistory: () => dispatch(undoHistory()),
  redoHistory: () => dispatch(redoHistory()),
  dispatch
});

const mergeProps = ({
  state,
  selectedToplevelNodes,
  ...restStateProps
}, {
  dispatch,
  ...restDispatchProps
}, {
  commit
}) => {
  const updateGlobalState = globalStateUpdater(dispatch, state);
  return { ...restDispatchProps,
    ...restStateProps,
    commit: withGlobalState(commit, updateGlobalState),
    groupIsSelected: selectedToplevelNodes.length === 1 && selectedToplevelNodes[0].includes('group')
  };
};

export const EditMenu = compose(connect(mapStateToProps, mapDispatchToProps, mergeProps), withProps(() => ({
  hasPasteData: Boolean(getClipboardData())
})), withHandlers(basicHandlerCreators), withHandlers(clipboardHandlerCreators), withHandlers(layerHandlerCreators), withHandlers(groupHandlerCreators), withHandlers(alignmentDistributionHandlerCreators))(Component);